@import 'styles/shared';

.button {
  padding: 8px 16px;
  border-radius: 2px;
  background-color: $accent-color;
  transition: all $timing-function;
  text-align: center;
  display: inline-block;
  color: $white;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 18px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  min-width: 180px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);

  margin: 0 auto;
}

.button:hover,
.button:focus {
  background-color: $button-accent-color;
}
